import React, { useState } from 'react';

import { Formik } from 'formik';
import styled from 'styled-components';
import { MDBContainer, MDBInput, MDBTypography } from 'mdb-react-ui-kit';
import Layout from '../components/Layout';
import SEO from '../components/Seo';
import PageHeader from '../components/PageHeader';
import Socials from '../components/Socials';
import { postContact } from '../services/contact';
import {
  TButtonPrimary,
  TTextPrimary,
  TOutboundLinkText,
  TPageMain,
} from '../components/ThemedComponents';
import PageMain from '../components/PageMain';

const SPageMain = styled(PageMain)`
  height: 1000px;
`;

const SContactHeaderContainer = styled(MDBContainer)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const SContactHeaderH2 = styled(TTextPrimary)`
  text-align: left;
  margin: 5px 13px;
  font-family: 'Roboto', sans-serif;
  font-size: 3rem;
  line-height: 1.2;
  font-weight: 700;
`;

const SContactHeaderText = styled(TTextPrimary)`
  margin: 15px 16px;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  text-align: center;
`;

const SContactContainer = styled(TPageMain)`
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-bottom: 345px;
`;

const SContactH3 = styled(TTextPrimary)`
  font-family: 'Roboto', sans-serif;
  margin-top: 40px;
  text-align: center;
`;

const SForm = styled.form`
  width: 80%;
`;

const SInputContainer1 = styled.div`
  display: flex;
`;

const SContactInput = styled(MDBInput)`
  margin: 20px;
  margin-bottom: 40px;
`;

const SContactInput1 = styled.div`
  width: 100%;
  height: 80px;
  display: flex;
  flex-direction: column;
`;

const SContactInputSubject = styled(SContactInput)`
  height: 70px;
`;

const SContactInputMessage = styled(SContactInput)`
  margin-bottom: 10px;
  height: 150px;
`;

const SContactButton = styled(TButtonPrimary)`
  min-width: 98.2%;
`;

const SContactError = styled(MDBTypography)`
  margin: 4px;
  color: red;
`;

interface FormErrors {
  name: string;
  email: string;
  subject: string;
  message: string;
}

const Contact = () => {
  const [formSuccess, setFormSuccess] = useState(false);

  const handleFormSuccess = () => {
    setFormSuccess(true);
    setTimeout(() => {
      setFormSuccess(false);
    }, 1500);
  };
  return (
    <Layout>
      <SEO title="Contact Me" />
      <PageHeader>
        <SContactHeaderContainer>
          <SContactHeaderH2 variant="h2">Contact Me</SContactHeaderH2>
          <SContactHeaderText variant="p">
            Interested in hiring me for your project or just want to say hi? You
            can fill in the contact form below or send me an email to{' '}
            <TOutboundLinkText href="mailto:richard@richen.dev">
              richard@richen.dev
            </TOutboundLinkText>{' '}
            <br /> <br />
            Want to get connected? Follow me on the social channels below.
          </SContactHeaderText>
          <Socials />
        </SContactHeaderContainer>
      </PageHeader>
      <SPageMain>
        <SContactContainer>
          <SContactH3 variant="h3">Get In Touch</SContactH3>
          <Formik
            initialValues={{ name: '', email: '', subject: '', message: '' }}
            validate={values => {
              const errors: FormErrors = {
                name: '',
                email: '',
                subject: '',
                message: '',
              };
              if (!values.name) {
                errors.name = 'A Name is Required';
              }
              if (!values.subject) {
                errors.subject = 'A Subject is Required';
              }
              if (!values.message) {
                errors.message = 'A Message is Required';
              }
              if (!values.email) {
                errors.email = 'An Email is Required';
              } else if (
                !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
              ) {
                errors.email = 'Invalid email address';
              }
              return errors;
            }}
            onSubmit={async (values, { setSubmitting }) => {
              await postContact(values);
              handleFormSuccess();
              setTimeout(() => {
                alert(JSON.stringify(values, null, 2));
                setSubmitting(false);
              }, 400);
            }}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              isSubmitting,
            }) => (
              <SForm id="contact-form" onSubmit={handleSubmit}>
                <SInputContainer1 id="contact-input-container-1">
                  <SContactInput1>
                    <SContactInput
                      className="contact-input"
                      placeholder="Name"
                      type="name"
                      name="name"
                      value={values.name}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    {errors.name && touched.name ? (
                      <SContactError className="contact-error">
                        {errors.name}
                      </SContactError>
                    ) : (
                      <SContactError className="contact-error"> </SContactError>
                    )}
                  </SContactInput1>
                  <SContactInput1>
                    <SContactInput
                      className="contact-input"
                      placeholder="Email"
                      type="text"
                      name="email"
                      value={values.email}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    {errors.email && touched.email ? (
                      <SContactError className="contact-error">
                        {errors.email}
                      </SContactError>
                    ) : (
                      <SContactError className="contact-error"> </SContactError>
                    )}
                  </SContactInput1>
                </SInputContainer1>
                <SContactInputSubject
                  className="contact-input"
                  placeholder="Enter your subject"
                  type="text"
                  name="subject"
                  value={values.subject}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                {errors.subject && touched.subject ? (
                  <SContactError className="contact-error">
                    {errors.subject}
                  </SContactError>
                ) : (
                  <SContactError className="contact-error"> </SContactError>
                )}
                <SContactInputMessage
                  textarea
                  rows={6}
                  className="contact-input"
                  placeholder="Enter your message"
                  type="text"
                  name="message"
                  value={values.message}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                {errors.message && touched.message ? (
                  <SContactError className="contact-error">
                    {errors.message}
                  </SContactError>
                ) : (
                  <SContactError className="contact-error"> </SContactError>
                )}

                <SContactButton type="submit" disabled={isSubmitting}>
                  Send Now
                </SContactButton>
                {formSuccess ? (
                  <div
                    style={{
                      color: 'green',
                      paddingLeft: '10px',
                      textAlign: 'center',
                    }}
                  >
                    Thank You! Your message will be replied to shortly!
                  </div>
                ) : (
                  <div
                    style={{
                      color: 'green',
                      padding: '14px',
                    }}
                  />
                )}
              </SForm>
            )}
          </Formik>
        </SContactContainer>
      </SPageMain>
    </Layout>
  );
};

export default Contact;
